.App {
  text-align: center;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.Menu {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Menu input, button {
  width: 200px;
  height: 30px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  font-size: 20px;
}
.nextBtn {
  background-color: rgba(133, 25, 196, 0.174);
  border: 1px solid white;
}
button:hover, .nextBtn:hover  {
    cursor: pointer;
    background-color: rgba(133, 25, 196, 0.274);
}
.answerBtn {
  height: auto;
}

.Quiz {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.endScreen {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}